.links-page_container {
    position: relative;
    width: 100%;
    min-height: 200vh;

    display: flex;
    justify-content: space-between;
    align-items: center;


    background-image:
        linear-gradient(to bottom, rgba(35, 51, 75, 1), rgba(33, 66, 116, 0.5)),
        url('images/background-links.jpg');

    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;

    z-index: 5;
}

.links-page_inside_container {
    padding-top: 140px;
}