    left: 0;
    right: 0;
    margin: auto;
    width: 0%;
    content: '.';
    color: transparent;
    background: #000;
    height: 1px;
} */

.li_menu_main_item:hover:after {
    width: calc(100%);
}

.link_main_nav.active {
    background: #000;
    border-radius: 25px;
    padding: 20px;
    margin: 0;
    text-transform: uppercase;
    box-sizing: border-box;
    opacity: 1;
}

.link_main_nav.active span {
    color: #fff;
}

.link_main_nav:hover {
    opacity: 1;
}

.link_main_nav {
    position: relative;
    background: none;
    border: none;
    margin: 0;
    padding: 20px;
    text-decoration: none;
    color: #fff;
    letter-spacing: 2px;
    font-weight: 300;
    transition: .6s;
    cursor: pointer;
    opacity: .6;
}

.link_main_nav span {
    margin: 0;
    padding: 0;
}

.link_main_nav:after {
    transform: scaleX(0);  
    transition: transform 250ms ease-in-out;
}

.link_main_nav:hover:after {
    transform: scaleX(1);
}

.nav_main.sticky .link_main_nav {
    color: #000;
}
.welcome-page_container {
    position: relative;
    width: 100%;
    min-height: 100vh;

    display: flex;
    justify-content: space-between;
    align-items: center;

    background-color: #051937;
    background-size: contain;
    background-repeat: no-repeat;
}

.particles {
    z-index: 4;
    height: 100vh;
}

.description_container {
    width: 50%;
    margin: 220px 0 0 30px;
    padding: 0 20px 40px 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.header_welcome {
    color: #fff;
    text-align: center;
    font-size: 2.5em;
    line-height: 75px;
    margin: 0;
}

.header_welcome span {
    color: #27d4cf;
    font-size: 1.5em;
}

.p_welcome-description {
    color: #fff;
    text-align: center;
    font-size: 1em;
    line-height: 20px;
    margin: 20px 0;
    padding: 20px;
    border: 1px solid #fff;
    border-radius: 25px;
}

.link_about {
    position: relative;
    background: none;
    border: none;
    margin: 0;
    padding: 20px;
    text-decoration: none;
    color: #fff;
    letter-spacing: 2px;
    font-weight: 300;
    transition: .5s;
    cursor: pointer;
    z-index: 5;
}

.link_about:hover {
    background: #fff;
    color: #000;
    border-radius: 25px;
}

.link_about span {
    margin: 0;
    padding: 0;
}

.profile-picture_container {
    margin-top: 220px;
    padding: 0;
    z-index: 5;
}

.img_profile-picture {
    height: calc(100vh - 260px);
    width: auto;
    border-radius: 50px;
    margin-bottom: 40px;
    opacity: .75;
}

.ul_social-media {
    list-style-type: none;
    z-index: 5;
    padding: 0 20px 40px 20px;
    margin: 220px 0 0 0;
}

.ul_social-media li {
    background: #fff;
    border-radius: 10px;
    margin: 10px 0;
    display: flex;
    align-items: center;
    transition: .5s;
    opacity: .45;
}

.ul_social-media:hover li {
    opacity: .85;
}

.ul_social-media li a {
    height: 50px;
    width: 60px;
}

.icon_social-media_container {
    background: linear-gradient(89deg, #CFD6E6 0%, #E7EFF9  100%);
    /* box-shadow: 10px 10px #000; */
}

.icon_social-media {
    height: 40px;
    width: auto;
    margin: 5px 10px;
}

.icon_social-media:hover {
    cursor: pointer;
}
.about-page_container {
    position: relative;
    width: 100%;
    min-height: 100vh;
    padding-top: 140px;

    display: flex;
    justify-content: center;
    align-items: center;

    background-image:
        linear-gradient(to bottom, rgba(5, 25, 55, 1), rgba(33, 66, 116, 0.5)),
        url(/resume/static/media/background-about.d078968b.jpg);

    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;

    z-index: 5;
}

.about-page_inside_container {
    width: 90%;
    position: relative;
    display: flex;
    justify-content: space-between;
}

.about-page_left_container {
    width: 35%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.about-page_right_container {
    width: 65%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-page_card_container {
    position: relative;
}

.about-page_left_container .face {
    width: 175px;
    height: 175px;
    transition: 0.5s;
}

.about-page_right_container .face {
    width: 675px;
    height: 200px;
    transition: 0.5s;
}

.face.face1 {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 6;
    transition: 1s;
    transform: translateY(100px);
}

.about-page_card_container:hover .face.face1 {
    background: #000;
    transform: translateY(0);
}

.content {
    opacity: .6;
    transition: .5s;
    text-align: center;
}

.img_about-page_card {
    max-width: 100px;
    -webkit-filter: invert(1);
            filter: invert(1);
}

.content h3 {
    margin: 10px 0 0;
    padding: 0;
    color: #fff;
    text-align: center;
    font-size: 1.5em;
}

.about-page_card_container:hover .content {
    opacity: 1;
}

.face.face2 .content {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.about-page_card_container:hover .img_about-page_card {
    max-width: 100px;
}

.face.face2 {
    position: relative;
    background: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 5px;
    box-sizing: border-box;
    transform: translateY(-100px);
    opacity: 0;
}

.about-page_card_container:hover .face.face2 {
    transform: translateY(0);
    opacity: 1;
}

.face.face2 p {
    margin: 0;
    padding: 0;
}

.personal-info {
    margin: 0;
    padding: 0;
    font-size: 0.8em;
    line-height: 2em;
    line-break: auto;    
    vertical-align: middle;
    display: inline-block;
}

.personal-info_name {
    transition: ease-in 1s;
    opacity: 0;
}

.personal-info_birthdate {
    transition: ease-in 2s;
    opacity: 0;
}

.personal-info_birthplace {
    transition: ease-in 2.5s;
    opacity: 0;
}

.about-page_personal-info_container:hover .personal-info_name, 
.about-page_personal-info_container:hover .personal-info_birthdate,
.about-page_personal-info_container:hover .personal-info_birthplace {
    opacity: 1;
}

.personal-info span {
    font-size: 1.5em;
    color: #27d4cf;
    font-weight: 700;
    text-transform: uppercase;
}

.education-name {
    margin: 0;
    padding: 0;
    padding-bottom: 5px;
    font-size: 1em;
    line-height: 1.1em;
    line-break: auto;
    transition: ease-in 1s;
    font-weight: 500;
    vertical-align: middle;
    display: inline-block;
    border-bottom: 1px solid #000;
    opacity: 0;
}

.education-name span {
    color: #27d4cf;
    font-weight: 700;
}

.education-info {
    width: 100%;
    margin: 0;
    padding: 5px 0;
    line-height: 1.1em;
    line-break: auto;
    transition: ease-in 2s;
    vertical-align: middle;
    display: inline-block;
    border-bottom: 1px solid #000;
    opacity: 0;
}

.education-info span {
    color: #27d4cf;
    font-weight: 700;
}

.education-time {
    width: 100%;
    margin: 0;
    padding: 5px 0;
    line-height: 1.1em;
    line-break: auto;
    transition: ease-in 2.5s;
    vertical-align: middle;
    display: inline-block;
    opacity: 0;
}

.education-time span {
    color: #27d4cf;
    font-weight: 700;
}

.about-page_education_container:hover .education-name,
.about-page_education_container:hover .education-info,
.about-page_education_container:hover .education-time {
    opacity: 1;
}

.hobbies_flexbox {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.hobbies-column {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    border-right: 1px solid #000;
}

.hobbies-column:last-child {
    border: none;
}

.hobby-title {
    height: 20%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hobby-title p {
    font-size: 1.2em;
    font-weight: 700;
    transition: 1s;
    opacity: .35;
}

.hobbies-column:hover .hobby-title p {
    color: #27d4cf;
    opacity: 1;
}

.hobby-description {
    height: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: .8em;
    padding: 0 5px;
}

.hobby-description p {
    transition: 1s;
    opacity: .35;
}

.hobbies-column:hover .hobby-description p {
    opacity: 1;
}
